<template>
  <div style="padding: 2%">
    <el-button type="primary" @click="exportExcel" style="float: left"
      >新增</el-button
    >
    <el-table
      id="out-table"
      :data="tableData"
      style="width: 100%; margin-top: 4%"
    >
      <el-table-column prop="id" label="序号"></el-table-column>
      <el-table-column prop="user_type" label="图片">
        <template slot-scope="scope">
          <img
            :src="'/public/static/img/' + scope.row.icon"
            style="width: 100px; height: 100px"
            alt=""
          />
        </template>
      </el-table-column>
      <el-table-column prop="title" label="标题"></el-table-column>
      <el-table-column prop="title" label="操作" width="200">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)" type="warning">编辑</el-button>
          <el-button @click="del(scope.row)" type="warning">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <el-upload
        style="margin-top: 20px"
        class="avatar-uploader"
        action="/uploadsFile"
        name="file"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
      >
        <img v-if="value" :src="'/public/static/img/' + value" class="avatar" />
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <el-select style="width: 300px; margin-top: 20px;" v-model="type1_id" placeholder="请选择">
        <el-option
          v-for="item in type1_list"
          :key="item.title"
          :label="item.title"
          :value="item.id"
        >
        </el-option>
      </el-select>
      <el-input
        style="width: 300px; margin-top: 20px; display: block"
        placeholder="请输入分类标题"
        v-model="title"
      />

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sub">确 定</el-button>
      </span>
    </el-dialog>
    <div style="float: right; margin-top: 4%; padding-bottom: 4%">
      <el-pagination
        @current-change="handleCurrentChange"
        layout="prev, pager, next, jumper"
        :total="pages * 10"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import FileSaver from "file-saver";
import XLSX from "xlsx";

export default {
  created() {
    this.get_list(this.page);
    axios.post("/type1", { queryall: "queryall" }).then((res) => {
      this.type1_list = res.data.data;
    });
  },
  data() {
    return {
      tableData: [],
      value: "",
      dialogVisible: false,
      edit_id: null,
      title: "",
      radio1: 1,
      type1_list: [],
      type1_id: null,
      pages: 1,
        page: 1,
    };
  },
  methods: {
    // 打开编辑
    edit(e) {
      this.value = e.icon;
      this.title = e.title;
      this.dialogVisible = true;
      this.edit_id = e.id;
      this.type1_id = e.type1_id
    },

    // 删除
    del(e) {
      this.$confirm("此操作将永久删除, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          axios
            .post("/type2", { del_id: e.id })
            .then((res) => {
              if (res.data.code == 200) {
                this.get_list(this.page);;
              } else {
                this.$message.error("操作失败");
              }
            });
        })
        .catch(() => {});
    },

    sub() {
      let obj = { icon: this.value, title: this.title, type1_id: this.type1_id };
      if (this.edit_id) {
        obj.edit_id = this.edit_id;
      } else {
        obj.add = "add";
      }
      axios.post("/type2", obj).then((res) => {
        this.edit_id = null;
        this.dialogVisible = false;
        this.get_list(this.page);;
      });
    },

    handleCurrentChange(currentPage) {
      this.get_list(currentPage);
    },

    exportExcel() {
      this.dialogVisible = true;
    },

    get_list(limit) {
      axios.post("/type2", { query: "query", page: limit }).then((res) => {
        this.tableData = res.data.data;
      });
    },
    // 上传图片
    handleAvatarSuccess(res, file) {
      this.value = res;
    },
  },
};
</script>

<style scoped>
.ccc {
}
</style>


